import React,{Component} from 'react'
import { Form, Select, Input, Button, message, Icon } from 'antd';
const FormItem = Form.Item;
const Option = Select.Option;

export class SearchForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            keyWord: "",
            type: "",
            status: ""
        };
        ['handleSubmit',  'resetForm'].forEach(m=>this[m] = this[m].bind(this))
    }

    handleSubmit(page) {
        let that = this;
        this.props.form.validateFields((err, values) => {
            that.props.showLoading();
            ajax({
                url: "/fund/normal/getInfoList",
                method: "post",
                data: Object.assign(values, {
                    current: page
                }),
                callback: function(data) {
                    that.props.hideLoading();
                    if(data.status == 200) {
                        that.props.searchFuc(data.data);
                    } else {
                        message.error(data.errMsg, 1);
                    }
                }
            })
        })
    }
    resetForm() {
        this.props.form.resetFields();
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        return (
            <Form layout="inline">
                <FormItem
                    label="关键字"
                >
                    {getFieldDecorator('keyWord')(
                        <Input placeholder="请输入关键字进行查询" />
                    )}
                </FormItem>
                <FormItem
                    label="资金类别"
                >
                    {getFieldDecorator('type', {
                        initialValue: "0"
                    })(
                        <Select>
                            <Option value="0">全部</Option>
                            <Option value="1">债权</Option>
                            <Option value="2">股权</Option>
                            <Option value="3">资产收购</Option>
                        </Select>
                    )}
                </FormItem>
                <FormItem
                    label="审核状态"
                >
                    {getFieldDecorator('auditStatus', {
                        initialValue: "0"
                    })(
                        <Select>
                            <Option value="0">所有</Option>
                            <Option value="1">待审核</Option>
                            <Option value="2">未通过</Option>
                            <Option value="3">已通过</Option>
                        </Select>
                    )}
                </FormItem>
                <FormItem>
                    <Button icon="search" type="primary"  onClick={this.handleSubmit.bind(this,1)} style={{"marginRight": "20px"}}>查询</Button>
                    <Button icon="reload" onClick={this.resetForm}>重置</Button>
                </FormItem>
            </Form>
        )
    }
}

const WrappedSearchForm = Form.create({})(SearchForm);

module.exports = WrappedSearchForm;


