import React,{Component} from 'react'
import { hashHistory } from 'react-router';
import { Layout, Breadcrumb, Button, Table, Popconfirm, message, Spin } from 'antd';
const { Content } = Layout;
import SearchForm from './Form/searchForm.jsx'
const selectOpsConfig = {
    catogoryList: {
        0: "所有",
        1: "债权",
        2: "股权",
        3: "资产收购"
    },
    auditStatusList: {
        0: "所有",
        1: "待审核",
        2: "未通过",
        3: "已通过"
    },
    progressList: {
        0: "进行中",
        1: "已结束"
    },
    userTypeList: {
        0: "资金方",
        1: "项目方"
    },
    typeList: {
        4: {
            0: "fdgdf",
            1: "dgdfgf",
            2: "fdhgfhfg"
        },
        1: {
            0:"11123",
            1: "grthjytht",
            2: "rgrhtt"
        },
        2: {
            0: "gehthtr",
            1: "gdtrtr",
            2: "gegtrgr"
        },
        3: {
            0: "gehthtr",
            1: "gdtrtr",
            2: "gegtrgr"
        }
    }
};
export class List extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            columns: [{
                title: '类别',
                dataIndex: 'category',
                render: category => <div>{selectOpsConfig.catogoryList[category]}</div>
            }, {
                title: '类型',
                render: data => <div>{selectOpsConfig.typeList[data.category][data.type]}</div>
            }, {
                title: '标题',
                dataIndex: 'title'
            }, {
                title: '审核状态',
                render: data => <div>{selectOpsConfig.auditStatusList[data.audit.status]}</div>
            }, {
                title: '进度',
                dataIndex: 'progress',
                render: progress => <div>{selectOpsConfig.progressList[progress]}</div>
            }, {
                title: '用户类型',
                dataIndex: 'userType',
                render: userType => <div>{selectOpsConfig.userTypeList[userType]}</div>
            }, {
                title: '发布人',
                dataIndex: 'publisher',
                render: publisher =>
                    <div className="divClick" onClick={this.goToUserDetail.bind(this, publisher.id)}>{publisher.name}</div>
            }, {
                title: "发布时间",
                dataIndex: 'publisherTime'
            }, {
                title: "操作",
                render: data => <div style={{"textAlign": "center"}}>
                    <Button type="primary" onClick={this.detailInfo.bind(this, data)} style={{"marginRight": "20px"}}>查看
                    </Button>
                    <Button onClick={this.auditInfo.bind(this, data)} style={{"marginRight": "20px"}}>审核
                    </Button>
                    <Popconfirm placement="left" title="是否确认删除?" onConfirm={this.deleteInfo.bind(this, data.key)} okText="确认" cancelText="取消">
                        <Button type="danger">删除</Button>
                    </Popconfirm></div>
            }],
            dataSource: [],
            pagination: {
                current: 1,
                total: 0
            },
            loading: false
        };
        ['goToUserDetail', 'changePage', 'handleSearch', 'showLoading', 'hideLoading'].forEach(m=>this[m] = this[m].bind(this))
    }

    goToUserDetail(id) {
        hashHistory.push("/user/manage/detail/" + id);
    }

    componentDidMount() {
        this.formRef.handleSubmit(1)
    }

    showLoading() {
        this.setState({loading: true});
    }

    hideLoading() {
        this.setState({loading: false});
    }

    detailInfo(data) {
        hashHistory.push("/fund/normal/detail/" + data.key + "?assetsType=" + data.category);
    }

    auditInfo(data) {
        hashHistory.push("/fund/normal/audit/" + data.key + "?assetsType=" + data.category);
    }

    deleteInfo(key) {
        let that = this;
        ajax({
            url: "/fund/normal/deleteInfo",
            method: "post",
            data: {id: key},
            callback: function(data) {
                if(data.status == 200) {
                    message.success("删除成功", 1);
                    let newDataSource = that.state.dataSource.filter(item => { return item.key != key});
                    that.setState({dataSource: newDataSource});
                } else {
                    message.error(data.errMsg, 1);
                }
            }
        });
    }

    handleSearch(result) {
        this.setState(
            {
                pagination: {
                    current: result.current,
                    total: result.total
                },
                dataSource: result.list
            }
        )
    }

    changePage(pagination) {
        this.formRef.handleSubmit(pagination.current);
    }

    render() {
        return (
            <Layout style={{ padding: '0 24px 24px' }}>
                <Breadcrumb style={{ margin: '12px 0' }}>
                    {
                        this.props.routes.map(function(item, index) {
                            return <Breadcrumb.Item key="">{item.breadcrumbName}</Breadcrumb.Item>
                        })
                    }
                </Breadcrumb>
                <Content style={{ background: '#fff', padding: 24, margin: 0, minHeight: 280 }}>
                    <Spin spinning={this.state.loading} tip="Loading...">
                        <div style={{"marginBottom": "30px"}}>
                            <SearchForm searchFuc={this.handleSearch} showLoading={this.showLoading} hideLoading={this.hideLoading} wrappedComponentRef={(inst) => this.formRef = inst} />
                        </div>
                        <Table
                            bordered
                            dataSource={this.state.dataSource}
                            columns={this.state.columns}
                            pagination={this.state.pagination}
                            onChange={this.changePage}
                        />
                    </Spin>
                </Content>
            </Layout>
        )
    }
}

module.exports = List;


